import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew, BtnEdit, BtnDelete } from 'components/common/button';
import { Card, Row, Popconfirm, Col, message } from 'antd';
import { TableData } from 'components/common';
import { getRatingCertificates, deleteRatingCertificate } from 'services/ratingCertificate';

class RatingCertificatePage extends Component {
  state = { reload: 0 };

  columns = [
    {
      title: 'Xp hng',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Tn ting anh',
      dataIndex: 'labelInEnglish',
      key: 'labelInEnglish',
    },
    {
      title: 'T im',
      dataIndex: 'fromGrade',
      key: 'fromGrade',
    },
    {
      title: 'n im',
      dataIndex: 'toGrade',
      key: 'toGrade',
    },
    {
      title: 'Thao tc',
      dataIndex: 'operation',
      render: (text, record) => (
        <Fragment>
          <BtnEdit href={`#/rating-certificate/form/edit/${record.id}`} />
          <Popconfirm
            title="Bn c mun xa?"
            okText="Xa"
            cancelText="Hy"
            onConfirm={() => this.handleDelete(record.id)}
          >
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ),
    },
  ];

  handleDelete = async id => {
    try {
      await deleteRatingCertificate(id);
      message.success('Xa thnh cng');
      this.setState({ reload: (this.state.reload += 1) });
    } catch (err) {
      message.error('Xa khng thnh cng');
    }
  };

  render() {
    const { reload } = this.state;
    return (
      <Card title="Xp loi chng ch">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/rating-certificate/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              bordered
              columns={this.columns}
              service={getRatingCertificates}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <RatingCertificatePage />
  </Page>
);



